import React, { Component, useState, Fragment } from 'react'
import {Toast} from 'antd-mobile'
import methods from '@utils/method'
import queryString from 'query-string'

// import 'font-awesome/css/font-awesome.css'
import './index.scss'

export default class FixedTool extends Component {
    constructor(props) {
        super(props);
        this.state = {
            compList: [],
            isShowFeedback: false,
            isShowPrintTip: false,
            printTipValue: '',
        }
        // this.blueToolStep1 = this.blueToolStep1.bind(this)
        // this.blueToolStep2 = this.blueToolStep2.bind(this)
    }
    componentDidMount() {
        this.init();

        // ArrayBuffer转16进度字符串示例
        function ab2hex(buffer) {
            var hexArr = Array.prototype.map.call(
                new Uint8Array(buffer),
                function(bit) {
                    return ('00' + bit.toString(16)).slice(-2)
                }
            )
            return hexArr.join('');
        }
        // wx.onBluetoothDeviceFound(function(res) {
        //     var devices = res.devices;
        //     // console.log('new device list has founded')
        //     if (devices && devices[0] && devices[0].advertisData) {
        //         console.log(JSON.stringify(devices))
        //         console.log('222--', ab2hex(devices[0].advertisData))
        //     }
        // })
        
    }

    clickPrint() {
        let { printValue } = this.props;
        if (!printValue) {
            Toast.info('还未生成卡片，不能打印', 3);
            return;
        }
        if (!methods.isMiniprogram) {
            console.log(231)
            // 打印设置https://www.cnblogs.com/GmrBrian/p/6152943.html?utm_source=itdadao&utm_medium=referral      
            // window.print();
            document.execCommand('print')
        } else {
            this.props.hideForm();
            this.tipPrintShow()
            // this.blueToolStep1()
          
          
            // wx.createBLEConnection()
            // offBluetoothDeviceFound
        }
    }

    tipPrintShow() {
        let { query, printValue } = this.props;
        let allQuery = {
            ...query,
            defaultV: printValue
        }
        // let host = 'https://ay-seeing.github.io/';
        let host = 'https://ay-seeing-github-io.now.sh/';
        let path = 'detail.html'
        let url = `${host}${path}?${queryString.stringify(allQuery)}`;
        this.setState({
            isShowPrintTip: true,
            printTipValue: url,
        })
    }

    blueToolStep1() {
        let that = this
        wx.openBluetoothAdapter({
            success: function (res) {
                console.log('res', res)
                console.log('初始化蓝牙适配器成功')
                // callback && callback()
                that.blueToolStep2()
                
            },
            fail: function (res) {
                console.log('res', res.errMsg)
              console.log('请打开蓝牙和定位功能')
            //   that.setData({
            //     info: '请打开蓝牙和定位功能'
            //   })
            }
        })
    }

    blueToolStep2() {
        let that = this;
        setTimeout(() => {
            // console.log('sd')
            wx.offBluetoothDeviceFound()
        }, 2000);
        
        wx.getBluetoothAdapterState({
            success: function (res) {

                //打印相关信息
                console.log(res)
                console.log(JSON.stringify(res.errMsg) + "\n蓝牙是否可用：" + res.available);
                that.blueToolStep3()
                // callback && callback()
                //   that.setData({
                //     info: JSON.stringify(res.errMsg) +"\n蓝牙是否可用：" + res.available
                //   })

            },
            fail: function (res) {

                //打印相关信息
                console.log(JSON.stringify(res.errMsg) + "\n蓝牙是否可用：" + res.available);

                //   that.setData({
                //     info: JSON.stringify(res.errMsg) + "\n蓝牙是否可用：" + res.available
                //   })

            }
            
        })
    }

    blueToolStep3() {
        let that = this;
        wx.startBluetoothDevicesDiscovery({
            // services: ['FEE7'], //如果填写了此UUID，那么只会搜索出含有这个UUID的设备，建议一开始先不填写或者注释掉这一句
            success: function (res) {
            //   that.setData({
            //     info: "搜索设备" + JSON.stringify(res),
            //   })
                console.log('搜索设备返回' + JSON.stringify(res))
                setTimeout(()=>{
                    that.blueToolStep4()
                }, 3000)
            }
          })
    }

    blueToolStep4() {
        let that = this;
        wx.getBluetoothDevices({
            success: function (res) {
       
            //   that.setData({
            //     info: "设备列表\n" + JSON.stringify(res.devices),
            //     devices: res.devices
            //   })
              console.log('搜设备数目：' + res.devices.length)
              console.log('设备信息：\n' + JSON.stringify(res.devices)+"\n")
            }
          })
    }

    clickEditor() {
        console.log('编辑')
    }

    clickFeedback() {
        this.props.hideForm();
        this.setState({
            isShowFeedback: true,
        })
    }

    clickCopy() {
        let { printTipValue } = this.state;
        if (methods.isMiniprogram && printTipValue) {
            wx.setClipboardData({
                //准备复制的数据
                data: printTipValue,
                success: function (res) {
                    wx.showToast({
                        title: '复制成功',
                    });
                }
            });
        }
    }

    clickClose() {
        this.props.showForm();
        this.setState({ isShowPrintTip: false });
        this.setState({ isShowFeedback: false });
    }
    
    init() {
        let { compList=[] } = this.props;
        let PrintTool = <div className="icon icon_print" onClick={()=>this.clickPrint()}>打印</div>;
        let EditorTool = <div className="icon icon_editor" style={{display: 'none'}} onClick={this.clickEditor}>编辑</div>;
        let Feedback = <div className="icon icon_feedback" onClick={()=>this.clickFeedback()}>反馈</div>;
        let toolList = [];
        toolList.push(PrintTool, EditorTool, Feedback);
        
        this.setState({
            compList: [...toolList, ...compList],
        });
    }

    render() {
        let { className = '' } = this.props;
        let { isShowFeedback, isShowPrintTip, printTipValue } = this.state;
        let { compList } = this.state;
        // console.log('comp', compList)
        return (
            <Fragment>
                <div className={`fixed_tool ${className}`}>
                    {compList.length > 0 && compList.map((tool, i) => {
                        return <div key={i}>{tool}</div>
                    })}
                </div>
                {isShowFeedback && <div className="fixed_layer fixe_feedback">
                    <div className="layer">
                        <h3>你可以通过添加微信或者邮箱反馈</h3><br />
                        <h4>微信</h4>
                        <div className="wechat"><img src="https://ae01.alicdn.com/kf/U06fd26627db24f5d9d5373ccd7116339R.jpeg" mode="widthFix" /></div><br />
                        <h4>邮箱</h4>
                        <div className="email">630999015@qq.com</div>
                        <div className="close" onClick={() =>this.clickClose() }>关闭</div>
                    </div>
                </div>}
                {isShowPrintTip && <div className="fixed_layer fixe_feedback">
                    <div className="layer">
                        <p className="mb10">小程序环境目前不支持打印，你可以“使用浏览器”访问下面的页面进行打印</p>
                        <div className="tip">{methods.isMiniprogram ? <wx-text selectable='true'>{printTipValue}</wx-text> : <div>{printTipValue}</div>}</div>
                        <div className="center"><div className="btn btn_small mtb10" onClick={()=>this.clickCopy()}>点击复制</div></div>
                        <div className="close" onClick={() => this.clickClose() }>关闭</div>
                    </div>
                </div>}
            </Fragment>)
    }
}
